import React from 'react';

class Home extends React.Component
  {
    state={
      text:'',
      myWishes: [{_id: 1,wish:'loading'}]
    }
    componentDidMount()
    {
      fetch('/data')
      .then(res=> res.json())
      .then(res2 => {
        this.setState({ myWishes: res2});
      })
    }

    handleDelete(data)
    {
      fetch('/remove/' +data, {method:'delete'})
      .then(res => res.json())
      .then(res2 => {
         const newWishes = this.state.myWishes.filter((item) =>
         {
           return item._id !== res2._id
         })
         this.setState({ myWishes: newWishes})
      })
    
    }
    handleSubmit(e)
    {
      e.preventDefault();
      //const url = "http://localhost:5000/sent";
      var data = new URLSearchParams();
       for(const pair of new FormData(e.target)){
          // console.log(pair)
         data.append(pair[0],pair[1])
       }

      fetch('/sent',{
          method:"post",
          body:data,
         
      }).then(res=>res.json())
      .then(res2 => {
        this.setState({ 
          myWishes:[...this.state.myWishes, res2]
        })
      }); 
    }
    render()
    {
      var displayWish = this.state.myWishes.map((item) =>
      {
        return  <a className="collection-item" key={item._id} onClick={() =>this.handleDelete(item._id)} >{item.wish}</a>
      }) 
      return(
            <div>
              <form onSubmit={(e) =>this.handleSubmit(e)}> 
                <input type='text' 
                  name='item' 
                  value={this.state.text} 
                  onChange={(e) => this.setState({ text: e.target.value})}/>
                <button type='submit' className="waves-effect waves-light btn">Add</button>
              </form>

              <div className="collection">
                {displayWish}
               </div>
            
            </div>
      );
    }
    
  }

  export default Home;